.WorkflowDetails {
    padding: 5px;
    margin: 5px;
    border: 3px solid white;
    border-radius: 10px;
    height: auto;
    display: flex;
    flex-direction: column;

    .radio {
        flex-flow: wrap;

        .Btn {
            border: 2px solid gray;

            &.highlight {
                background-color: var(--highlight-color);
            }
        }
    }

    .title {
        font-size: 1.5em;
        margin-bottom: 5px;
    }

    .Label {
        color: white;
        text-align: left;
    }
}