
.Diagram .react-flow__edge {
    .connection-foreignobject {
        --body-size: 30px;
        --center-point: calc(var(--body-size) / -2);
        --button-size: 30px;
        min-height: var(--body-size);
        min-width: var(--body-size);
        height: var(--body-size);
        width: var(--body-size);
        transform: translate(var(--center-point), var(--center-point));
        overflow: visible;

        button {
            min-height: var(--button-size);
            min-width: var(--button-size);
            height: var(--button-size);
            width: var(--button-size);
            color: white;
            background-color: black;
            border: 2px solid gray;
            border-radius: 100px;
            position: relative;
            font-size: 0.5em;

            svg {
                position: absolute;
                height: calc(var(--button-size) * 1.2);
                width: calc(var(--button-size) * 1.2);
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                color: transparent;
            }
        }
    }

    &.animated path {
        @-webkit-keyframes dashdraw {
            0% {
                stroke-dashoffset: 20
            }
        }
        @keyframes dashdraw {
            0% {
                stroke-dashoffset: 20
            }
        }
        stroke-dasharray: 12 8;
        -webkit-animation: dashdraw 1s linear infinite;
        animation: dashdraw 1s linear infinite;
    }

    path.react-flow__edge-path {
        stroke: gray;
        stroke-width: 5;
    }

    &.react-flow__edge-smoothstep path.react-flow__edge-path {
        stroke: orange;
        stroke-width: 5;
    }

    path.react-flow__edge-path + polygon {
        fill: gray;
    }

    path.react-flow__edge-path.isSelected + polygon {
        fill: var(--color);
    }

    &.selected {
        .connection-foreignobject button {
            border: 4px solid transparent;

            svg {
                color: var(--color);
            }
        }

        .connection-arrowhead {
            fill: var(--color);
            stroke-width: 4;
            stroke: var(--color);
        }

        path.react-flow__edge-path {
            z-index: 9;
            stroke: var(--color);
            stroke-width: 7;
            @-webkit-keyframes dashdraw {
                0% {
                    stroke-dashoffset: 20
                }
            }
            @keyframes dashdraw {
                0% {
                    stroke-dashoffset: 20
                }
            }
            stroke-dasharray: 12 8;
            -webkit-animation: dashdraw 0.6s linear infinite;
            animation: dashdraw 0.6s linear infinite;
        }
    }
}

