.GenericObjectListComponent {
    padding: 3px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    color: white;
    border: 3px solid transparent;
    border-radius: 8px;
    margin-top: 4px;
    background-color: #55555566;

    &.highlight {
        border-color: #992222;
    }

    .header {
        font-weight: bold;
        font-size: 1.1em;
        line-height: 1.3em;
        margin: 3pt 0pt 3pt 0pt;
        position: relative;
        width: calc(100% - 0pt);
        display: inline-flex;
        justify-content: space-between;
        align-items: center;

        span {
            justify-self: center;
            margin-right: 3pt;
            justify-content: right;
        }

    }

    button, .Btn {
        width: 20px;
        height: 20px;
    }

    .ItemContainer {
        border: 3px solid transparent;
        background-color: rgba(100, 100, 100, .3);

        &.highlight {
            border: 3px solid rgb(0, 200, 255);
            border-radius: 7pt;
        }
    }
}

.GenericObjectComponent {

    border-radius: 4pt;
    //margin: 2pt 0pt;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    padding: 2pt 1pt;

    * {
        max-width: 100%;
    }
}