.PropertyPair {
    display: inline-flex;
    flex-direction: row;
    justify-content: stretch;
    flex: 1;
    min-width: 100px;

    input {
        flex: 1;
        font-size: 1em;
        line-height: 1em;
        background-color: #55555555;
        width: 50%;
        margin-left: 8px;
        border-right: none;
        color: #bbbbbb;

        &:focus {
            background-color: #55555599;
        }

        &:nth-child(1) {
            margin-left: 0;
        }
    }
}