.Property {
    margin: 1pt;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 0 3pt;

    &.json {
        flex-direction: column;
        min-height: 200px;
        max-height: 85vh;

        span:first-child {
            margin-bottom: 3px;
        }

        textarea {
            overflow-y: auto;
            min-height: 200px;
            width: 100%;
        }
    }

    span:first-child {
        color: white;
        width: 50%;
        text-align: left;

        .Property {
            padding-left: 0;
        }
    }

    input, textarea {
        color: white;
        background-color: #555555;
        min-width: 50%;
        padding: 1pt 3pt;

        &:disabled {
            color: white;
            background-color: transparent;
        }
    }

    &.SelectList {
        position: relative;

        .menu-btn {
            color: #ffe503;
            position: absolute;
            right: 0px;
            top: 40%;
            transform: translate(-0%, -50%);
        }

        .PopupMenu {
            bottom: 0px;
            transform: translateY(100%);
            min-width: 200pt;
            //border-top: none;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            background-color: black;
        }
    }
}
