.QuestionHook {
    //transform: translate(0, -100%);
    min-width: 250px;
    min-height: 30px;
    border: 4px solid orange;
    border-radius: 10px;
    padding: 10px;
    flex-direction: column;
    background-color: black;
    color: white;
    z-index: 2;

    > .header {
        justify-content: space-between;
        position: relative;
        font-size: 1.4em;
        margin: 0 0 8px;
        border-bottom: 1px solid grey;

        .Label {
            font-weight: bold;
            margin: auto 5px;
        }

        .Btn {
            //position: absolute;
            font-size: 1em;
            //right: 0;
            //top: 50%;
            //transform: translate(0%, -50%);
        }
    }
}

.react-flow__node-hook {
    z-index: 1 !important;
}

.Hook {
    margin-top: 4px;

    > .DropDownBtn {
        width: 100%;
        font-size: 1.1em;

        > .Btn {
            font-size: 1.1em;
            width: 100%
        }
    }

    > .Property {
        justify-content: flex-start;
        align-items: center;
        width: auto;
        margin-top: 4px;
        padding: 0;

        > span {
            white-space: nowrap;
            font-size: 1.1em;
            width: auto;
            margin: 0;
            flex: 1;
        }

        > input {
            width: auto;
            flex: 0;
            font-size: 1.1em;
            min-width: 80px;
        }

    }

    .Handle.HookSource {
        //content: "";
        //position: absolute;
        //bottom: -30px;
        /* vertically center */
        left: 50%;
        //border-radius: 0;
        transform: translate(-50%, 50%);
        stroke: orange;
        /* the arrow */
        border: 15px solid orange;
        //border-color: greenyellow transparent transparent transparent;
    }
}