body {
    --color-selected: #61dafb;
    --color-edited: #ffc400;
    --color: var(--color-selected);
}

*.selected {
    --color: var(--color-selected);
    --border-width: 5px;
}

*.edited {
    --color: var(--color-edited);
    --border-width: 10px;
}

.react-flow__node.react-flow__node-question .QuestionNode.selected,
.react-flow__node.react-flow__node-question.selected .QuestionNode,
.react-flow__node.react-flow__node-question .QuestionNode.edited,
.react-flow__node.react-flow__node-question.edited .QuestionNode {
    //border-color: #61dafb;
    overflow: visible;

    &:before {
        --margin: 10%;
        z-index: -1;
        top: calc(-1 * var(--margin));
        left: calc(-1 * var(--margin));
        content: '';
        position: absolute;
        width: calc(100% + var(--margin) + var(--margin));
        height: calc(100% + var(--margin) + var(--margin));
        border-radius: 8px;
        //border: 4px dashed #61dafb;
        //box-shadow: 0 0 0 8px #61dafb;

        background-image: linear-gradient(90deg, var(--color) 50%, transparent 50%),
        linear-gradient(90deg, var(--color) 50%, transparent 50%),
        linear-gradient(0deg, var(--color) 50%, transparent 50%),
        linear-gradient(0deg, var(--color) 50%, transparent 50%);
        background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;
        background-size: 15px var(--border-width),
        15px var(--border-width),
        var(--border-width) 15px,
        var(--border-width) 15px;
        background-position: left top, right bottom, left bottom, right top;
        animation: border-dance 0.8s infinite linear;

        @keyframes border-dance {
            0% {
                background-position: left top, right bottom, left bottom, right top;
            }
            100% {
                background-position: left 15px top, right 15px bottom, left bottom 15px, right top 15px;
            }
        }
    }
}

.QuestionNode {
    --type-color: white;
    min-width: 200px;
    max-width: 500px;
    padding: 14px 10px 40px 10px;
    //border: 2px solid #999999;
    border: 4px solid var(--type-color);
    border-radius: 8px;
    word-wrap: anywhere;

    background-color: black;
    color: white;
    box-shadow: 0 0 0 12px transparent;

    .HookName {
        height: 0;
    }

    .Hooks {
        text-align: right;

        span {
            word-wrap: normal;
            white-space: nowrap;
            position: absolute;
            transform: translate(-50%, -50%);
            top: 0;

            overflow: visible;
        }
    }

    .Output {
        > span {
            position: absolute;
            width: auto;
            min-width: 20px;
            word-wrap: normal;
            white-space: nowrap;
            transform: translate(-50%, -50%);
            font-size: 20px;
            left: 50%;
            top: -15px;

            &.Always {
                font-size: 2.5em;
                top: -10px
            }
        }
    }

    .Input {
        z-index: 1;

        > span {
            position: absolute;
            width: auto;
            height: auto;
            word-wrap: normal;
            white-space: nowrap;
            transform: translate(-50%, -50%);
            font-size: 15px;
            left: 50%;
            top: 38px;
        }
    }

    .Hooks {
        > span {
            position: absolute;
            width: auto;
            min-width: 20px;
            transform: translate(-100%, -50%);
            font-size: 16px;
            top: 50%;
            left: -10px;
        }
    }

    .QuestionNodeDetails {
        margin-top: -10px;

        .Icons {
            display: inline-flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            width: 100%;

            svg {
                font-size: 1.5em;
                margin: 5px;
            }

            .Btn {
                &.Edit {
                    margin-left: auto;
                }
            }
        }

        .Title {
            //margin-top: 10px;
            margin-bottom: 5px;
            font-size: 1.5em;
            font-weight: bold;
            line-height: 1em;
            vertical-align: bottom;
        }

        .Property {
            width: 100%;
            display: flex;

            span {
                white-space: nowrap;
                flex: 0;
                font-size: 1em;
                line-height: 1em;
                min-width: 20%;
                margin: auto 4px 5px 0;
            }

            input {
                flex: 1;
                font-size: 1em;
                line-height: 1em;
                background-color: #55555555;
                border-right: none;

                &:focus {
                    background-color: #55555599;
                }
            }
        }

        .Specific {
            width: 100%;
            min-width: 100px;
            padding: 5px;
            font-size: 1.5em;

            svg {
                font-size: 1.3em;
            }

            ul {
                display: flex;
                flex-direction: column;
                list-style: none;
                text-align: left;
                width: 100%;
                white-space: nowrap;

                li {
                    display: inline-flex;
                    width: 100%;
                    flex-direction: row;
                    flex-wrap: nowrap;
                    justify-content: stretch;

                    .Btn {
                        flex: 0;
                        min-width: 35px;
                    }
                }

                //
                //    li > div {
                //        display: inline-flex;
                //        flex-direction: row;
                //        justify-content: space-between;
                //        width: 100%;
                //        min-width: 1px;
                //
                //        *:last-child {
                //            margin-left: 5px;
                //        }
                //    }
            }
        }
    }
}
