.QuestionItem {
    --question-type-color: 'grey';
    border: var(--question-type-color) solid 2pt;
    margin: 5pt;
    padding: 3pt;
    border-radius: 5pt;
    height: auto;
    background-color: #101010;

    .key {
        color: var(--question-type-color);
        width: calc(100% - 10pt);
        margin: 0 5pt 3pt;
        padding-bottom: 5pt;
        font-weight: bold;
        font-size: 1.1em;
        line-height: 1.3em;
        border-radius: 5pt;
        display: inline-flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;

        > * {
            flex: 1;
            text-align: left;
        }
    }

    .title {
        padding-bottom: 5pt;
    }

    .wrapper {
        background-color: rgba(100, 100, 100, .2);
        border: 3px solid transparent;
        border-radius: 8px;

        &.highlight {
            border-color: #992222;
        }
    }

    .DropDownBtn {
        flex: 0;

        > .Btn {
            font-size: 1.2em;
            //width: 1.2em;
            background-color: transparent;
            color: #bbbbbb;

            &:hover {
                background-color: #55555588;
            }
        }
    }
}