.Btn {
    --highlight-color: red;
    min-width: 25px;
    min-height: 25px;
    line-height: 25px;
    border-radius: 5px;
    padding: 4px;
    margin: 2px;
    border: 3px solid transparent;
    background-color: transparent;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: auto;
    position: relative;

    .Label {
        flex-grow: 1;
        text-align: center;
    }

    .Icon {
        text-align: center;
        flex-grow: 0;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 0;

        svg {
            vertical-align: middle;
            font-size: 1.3em;
            line-height: 1.3em;
        }
    }

    .Label + .Icon {
        margin-left: 4px;
    }

    &:disabled {
        filter: brightness(0.5);
    }

    &:not(:disabled):hover {
        background-color: #555555;
    }

    &.highlight {
        border-color: var(--highlight-color);
    }

    &:hover .Label.Tooltip {
        --bg-color: #dddddd;
        background-color: var(--bg-color);
        color: black;
        opacity: 1;
        z-index: 10;
        padding: 2px 8px;
        text-wrap: none;
        white-space: nowrap;
        width: auto;
        transition-delay: 1s;

        &:before {
            content: "";
            position: absolute;
            top: -16px;
            /* vertically center */
            left: 16px;
            transform: translateX(-50%);
            /* the arrow */
            border: 8px solid var(--bg-color);
            border-color: transparent transparent var(--bg-color) transparent;
        }
    }

    .Label.Tooltip {
        display: inline-block;
        overflow: visible;
        opacity: 0;
        position: absolute;
        z-index: -1;
        top: 46px;
        left: calc(50% - 16px);
        transform: translateX(0%);
        transition: opacity;
        transition-delay: 0s;
    }
}
