.PopupMenu {
    z-index: 50;
    position: absolute;
    display: flex;
    flex-direction: column;
    background-color: rgba(30, 30, 30);
    border: 2px solid gray;
    overflow: visible;
    padding: 2px 3px;
    color: white;
    width: auto;

    .MenuItem {
        width: auto;

        .Hotkey {
            font-size: 0.95em;
            text-align: right;
            margin-right: 5px;
        }
    }

    .Separator {
        height: 0;
        margin: 5pt;
        border-bottom: 1px dashed gray;
    }

    .MenuTitle:not(:empty) {
        color: white;
        border-bottom: 1px dotted gray;
        padding-bottom: 2pt;
    }

    .Btn {
        position: relative;
        min-width: 30px;
        min-height: 30px;
        border: none;
        background-color: transparent;
        color: #999999;
        width: 100%;

        &:hover, &.highlight {
            background-color: #444444;
            color: white;
        }

        .Label.Tooltip {
            //display: none;
            top: 0;
            right: -8px;
            left: initial;
            transform: translateX(100%);
        }

        &:hover .Label.Tooltip:before {
            top: 50%;
            /* vertically center */
            left: -16px;
            transform: translateY(-50%);
            /* the arrow */
            border: 8px solid var(--bg-color);
            border-color: transparent var(--bg-color) transparent transparent;
        }
    }

    .MenuItem .PopupMenu {
        position: absolute;
        right: 0;
        top: -2px;
        transform: translate(100%, 0);
        border-top-left-radius: 0;

        &:after {
            display: none;
        }
    }

    &.float {
        transform: translate(-50%, -65px);
        border: 3px solid #e7d2ff;
        border-radius: 8px;
        overflow: visible;
        box-shadow: 0 0 0px 1000px rgba(0, 0, 0, 0.2);

        //button {
        //    color: #ffffff;
        //}

        &:after {
            --point-size: 15px;
            --point-top: calc(var(--point-size) * -2);
            --middle-compensate: calc(var(--point-size));
            content: "";
            position: absolute;
            top: var(--point-top);
            left: calc(50% - var(--middle-compensate));
            width: 0;
            height: 0;
            border: var(--point-size) solid transparent;
            border-bottom-color: #e7d2ff;
            box-shadow: 1px 1px 2px rgba(black, 0.2);
        }
    }
}