.ToolBar {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    //background-color: rgba(30, 30, 30);
    padding: 2px 3px;
    color: white;
    overflow: visible;

    .Separator {
        width: 0;
        height: 22px;
        margin-right: 5pt;
        margin-left: 5pt;
        border-right: 1px dotted #999999;
    }

    .Btn {
        border-color: transparent;
        background-color: transparent;
        color: #bbbbbb;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        position: relative;
        overflow: visible;

        &:disabled {
            color: #555555;
            filter: none;

            &:hover .Label {
                --bg-color: #999999;
            }
        }

        &:not(:disabled):hover {
            background-color: #444444;
            color: white;
            //border-color: white;
        }

        .Label.Tooltip {
        }
    }
}
