.Menu {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    background-color: #333333;
    overflow: visible;
    padding: 2px 3px;
    color: white;
    height: 35px;

    > .DropDownBtn {
        margin: 3px 0;
        position: relative;
        background-color: #555555;
    }

    .Label.filename {
        margin: 5px;
        right: 10px;
        top: 3px;
        z-index: 10;
        font-size: 1.1em;
        background-color: transparent;
    }

    .ToolBar {
        margin: 10px;
    }

    .Label.version {
        margin-right: 10px;
    }
}