.Diagram {
    flex: 2;
    background-color: #212121;
    overflow-y: auto;
    overflow-x: auto;
    position: relative;

    .react-flow__minimap {
        background-color: rgba(34, 34, 34, 0.9) !important;
    }

    .react-flow__controls .react-flow__controls-button {
        fill: white;
        stroke: white;
        background-color: #333333ee;
        border: slategray solid 0.5pt;

        &:hover {
            background-color: #444444;
        }
    }
}
