.Document {
    --color-Text: #1fd200;
    --color-Select: #0086ff;
    --color-Alert: #ff4800;
    --color-ThreeDimensions: #1fd200;
    --color-AutoAnswer: #aa70ff;
    --color-RoadlinkRequest: #ff67f0;
    --color-FunctionCall: #ffe503;
    --color-SingleDigit: #39ffa8;
    --color-Picture: #ff9514;
    --color-TimestampElapsed: #ff9514;
    --color-Timestamp: #ff9514;
    --color-TimestampAgeYears: #ff9514;

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    background-color: #000000;
    width: 100vw;
    height: 100vh;
    position: relative;

    > .views {
        display: inline-flex;
        height: 100%;
        width: 100%;
        flex-direction: row;
        flex-wrap: nowrap;

    }
}