.DropDownBtn {
    position: relative;

    > .Btn {
        position: relative;
        padding: 0 10pt;
        margin: 0;
        border-radius: 2px;
        border-color: transparent;

        .Label {
            text-align: left;
        }

        .Label + svg {
            margin-left: 5px;
        }

        &:hover {
            background-color: #bbbbbb;
        }
    }

    .PopupMenu {
        top: initial;
        min-width: 200pt;
        border-top: none;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
    }
}
