.RequestComponent {
    padding: 3px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    color: white;
    border: 3px solid transparent;
    border-radius: 8px;

    &.highlight {
        border-color: #992222;
    }

    .header {
        font-weight: bold;
        font-size: 1.1em;
        margin: 3pt 0pt 5pt 0pt;
        position: relative;
        width: calc(100% - 0pt);
        display: inline-flex;
        justify-content: space-between;
        align-items: center;

        span {
            justify-self: center;
            margin-right: 5pt;
            justify-content: right;
        }
        button {
            font-size: 1.2em;
            width: 20px;
            height: 20px;
            vertical-align: center;
            line-height: 1em;
            margin-left: 3px;
            border-radius: 5px;
            border: 3px solid transparent;

            &.highlight {
                border-color: rgb(0,200,255);
            }
        }
    }

    .ItemContainer {
        border: 3px solid transparent;
        background-color: rgba(100,100,100,.2);

        border-radius: 4pt;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        padding: 2pt 2pt;
        &.highlight {
            border: 3px solid rgb(0,200,255);
            border-radius: 7pt;
        }

        * {
            max-width: 100%;
        }
    }

    .PropertyObject > .header {
        font-size: 1em;
    }
}